import common from '@/../server/common'
import Login from './page/Login.vue'
import NoFind from './page/NoFind.vue'
import Home from './page/Home.vue'
import DataAdd from './page/Data/add.vue'
import DataList from './page/Data/list.vue'
import UpFileList from './page/UpFile/list.vue'
import userList from './page/User/list.vue'
import userAdd from './page/User/add.vue'

export default {
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '*',
      name: '/404',
      component: NoFind
    }, {
      path: '/',
      redirect: '/login'
    }, {
      path: '/login',
      name: 'login',
      meta: { title: '登录' },
      component: Login
    }, {
      path: '/data',
      meta: {
        verify: true,
        title: '污水管理',
        icon: 'fa fa-file-text-o'
      },
      component: Home,
      redirect: '/data/list',
      children: [{
        path: 'list',
        meta: {
          verify: true,
          grade: common.page_grade.listArticle,
          title: '公司列表',
          icon: 'fa fa-newspaper-o'
        },
        component: DataList
      }, {
        path: 'add',
        meta: {
          verify: true,
          title: '添加公司',
          icon: 'fa fa-clone'
        },
        component: DataAdd
      }, {
        path: 'edit/:id',
        meta: {
          verify: true,
          title: '信息修改',
          icon: 'fa fa-clone'
        },
        component: userAdd
      }]
    }, {
      path: '/user',
      meta: {
        verify: true,
        title: '用户管理',
        icon: 'fa fa-user-o'
      },
      redirect: '/user/list',
      component: Home,
      children: [{
        path: 'list',
        meta: {
          verify: true,
          grade: common.page_grade.userList,
          title: '用户列表',
          icon: 'fa fa-address-card-o'
        },
        component: userList
      }, {
        path: 'add',
        meta: {
          verify: true,
          grade: common.page_grade.updateUser,
          title: '添加用户',
          icon: 'fa fa-user-plus'
        },
        component: userAdd
      }, {
        path: 'edit/:id',
        meta: {
          verify: true,
          title: '编辑用户',
          icon: 'fa fa-user-times'
        },
        component: userAdd
      }]
    }, {
      path: '/upfile',
      meta: {
        verify: true,
        title: '上传管理',
        icon: 'fa fa-upload'
      },
      component: Home,
      redirect: '/upfile/list',
      children: [{
        path: 'list',
        meta: {
          verify: true,
          grade: common.page_grade.listUpFile,
          title: '上传列表',
          icon: 'fa fa-files-o'
        },
        component: UpFileList
      }]
    }
  ]
}
